                                    <div class="form-group">
                                        <label class="form-label" for="MontoPretencion">Monto de Ejecución 1</label>
                                        <input v-model="expediente.exp_monto_ejecucion1" type="number" class="form-control"
                                            id="MontoPretencion" placeholder="" />
