                                    </div>
                                </div>
                                <div class="row">
                                    <div class="col-md-4">
                                        <div class="form-group">
                                            <label class="form-label" for="nombres">Nombres</label>
                                            <input type="text" v-model="pnat.nat_nombres" class="form-control" id="nombres"
                                                placeholder="">
                                        </div>
                                    </div>
                                    <div class="col-md-4">
                                        <div class="form-group">
                                            <label class="form-label" for="telefono">Telefono</label>
                                            <input type="text" v-model="pnat.nat_telefono" class="form-control"
                                                id="telefono" placeholder="">
                                        </div>
                                    </div>
                                    <div class="col-md-4">
                                        <div class="form-group">
                                            <label class="form-label" for="email">Email
                                            </label>
                                            <input type="text" v-model="pnat.nat_correo" class="form-control" id="email"
                                                placeholder="">
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div v-else>
                                <div class="row">
