                                                distrito.dis_nombre }}
                                            </option>
                                        </select>
                                    </div>
                                </div>
                                <div class="col-md-6">
                                    <div class="form-group">
                                        <label class="form-label" for="calleAvenida">Calle, Avenida, Mz,
                                            S/N</label>
                                        <input type="text" class="form-control" id="calleAvenida" placeholder=""
                                            v-model="direccion.dir_calle_av">
                                    </div>
                                </div>
