                            <div class="row mb-2">
                                <div class="col-md-12">
                                    <div class="alert alert-primary py-3" role="alert">
                                        Datos del abogado
                                    </div>
                                </div>
                            </div>
                            <div class="row mb-2">
                                <div class="form-group">
                                    <label for="abogado" class="form-label">Abogado asignado al expediente en
                                        registro</label>
                                    <div class="row">
                                        <div class="col-md-12">
                                            <input type="text" disabled v-model="abogado" name="abogado" id="abogado"
                                                class="form-control">
                                        </div>
                                        <!-- <div class="col-md-3">
                                            <Button class="btn btn-primary btn-lg w-100" @click="update()" ><i data-feather="save"></i></Button>
                                        </div> -->
                                    </div>
                                </div>
                            </div>
                            <div class="row mb-2">
                                <div class="d-flex">
                                    <button @click="goBack" class="btn btn-lg btn-secondary px-3 rounded">
                                        <i data-feather="arrow-left"></i> Volver atras
                                    </button>
                                    <div class="ml-auto">
