                        </div>
                    </div>
                </div>
            </div>
            <!-- <div class="row">
                <div class="col-md-12">
                    <div class="card shadow">
                        <div class="card-body">
                            <div class="d-flex">
                                <button @click="goBack" class="btn btn-lg btn-secondary px-3">
                                    <i data-feather="arrow-left"></i> Volver atras
                                </button>
                                <div class="ml-auto">
                                    <Button class="btn btn-primary btn-lg" @click="update()"><i data-feather="save"></i>
                                        Guardar todos los cambios</Button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div> -->
        </div>
    </section>
</template>
<script>
import { mapActions } from "vuex";
import feather from 'feather-icons';

export default {
    name: 'RegistroProcesal',
    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },
    props: ['id'],
    data() {

        return {
            esPersonaNatural: true,
            expediente: {
                exp_id: "",
                exp_numero: "",
                exp_fecha_inicio: "",
                exp_monto_pretencion: "",
                exp_pretencion: "",
                exp_materia: "",
                exp_estado_proceso: "",
                exp_especialidad: "",
                exp_juzgado: "",
                exp_costos: "",
                exp_monto_ejecucion1: "",
                exp_monto_ejecucion2: "",
                exp_interes1: "",
                exp_interes2: "",
                persona: ""
            },
            distrito: [],
            instancia: [],
            especialidad: [],
            juzgado: {
                ins_id: "",
                dis_id: ""
            },
            departamentos: [],
            provincias: [],
            distritos: [],
            direccion: {
                dir_calle_av: "",
                dis_id: "",
                pro_id: "",
                dep_id: ""
            },
            pnat: {
                nat_id: "",
                nat_dni: "",
                nat_apellido_paterno: "",
                nat_apellido_materno: "",
                nat_nombres: "",
                nat_telefono: "",
                nat_correo: ""
            },
            pjuc: {
                jur_id: "",
                jur_ruc: "",
                jur_razon_social: "",
                jur_telefono: "",
                jur_correo: "",
                jur_rep_legal: ""
            },
            tipoper: "",
            procesal: "",
            abogado: "",
            juzgados: [

            ]
            , materias: [],
            pretenciones: []
        };
    },
    created() {
        this.listarMaterias();
        this.listarPretensiones();
        this.cargarDistritosJudiciales();
        this.cargarDepartamentos();
        this.traerexpediente();
    },
    methods: {
        ...mapActions(['get', 'post']),

        handleError(message, error) {
            // Manejo de errores centralizado
            console.error(message, error);
        },

        mostrarPersona(tipo) {
            this.tipoper = tipo;
            this.esPersonaNatural = tipo === "NATURAL";
        },

        async traerexpediente() {
            try {
                const response = await this.get({
                    url: `${this.$store.getters.get__url}/proceeding/${this.id}/show`,
                    token: this.$store.getters.get__token,
                    params: {}
                });

                // Datos de expediente
                const proceeding = response.proceeding;
                const person = response.person;
                const personData = response.personData;
                const costos = response.costos;

                Object.assign(this.expediente, {
                    exp_id: proceeding.exp_id,
                    exp_numero: proceeding.exp_numero,
                    exp_fecha_inicio: proceeding.exp_fecha_inicio,
                    exp_monto_pretencion: proceeding.exp_monto_pretencion,
                    exp_monto_ejecucion: proceeding.exp_monto_ejecucion,
                    exp_pretencion: proceeding.exp_pretencion,
                    exp_materia: proceeding.exp_materia,
                    exp_estado_proceso: proceeding.exp_estado_proceso,
                    exp_especialidad: proceeding.exp_especialidad,
                    exp_juzgado: proceeding.exp_juzgado,
                    persona: person.per_id,
                    exp_monto_consentido: proceeding.exp_monto_consentido,
                    exp_costos: proceeding.exp_costos,
                    exp_intereses: proceeding.exp_intereses
                });

                // Juzgado
                Object.assign(this.juzgado, {
                    dis_id: proceeding.exp_dis_judicial,
                    ins_id: proceeding.exp_instancia
                });

                this.cargarinstancias();
                this.cargarjuzgados();
                this.cargarespecialidades();

                // Demandante o demandado
                this.tipoper = response.tipo_persona;
                this.procesal = response.procesal;

                if (this.tipoper == 'natural') {
                    Object.assign(this.pnat, {
                        nat_id: person.nat_id,
                        nat_dni: personData.nat_dni,
                        nat_apellido_paterno: personData.nat_apellido_paterno,
                        nat_apellido_materno: personData.nat_apellido_materno,
                        nat_nombres: personData.nat_nombres,
                        nat_telefono: personData.nat_telefono,
                        nat_correo: personData.nat_correo
                    });
                } else {
                    Object.assign(this.pjuc, {
                        jur_id: person.jur_id,
                        jur_ruc: personData.jur_ruc,
                        jur_razon_social: personData.jur_razon_social,
                        jur_telefono: personData.jur_telefono,
                        jur_correo: personData.jur_correo,
                        jur_rep_legal: personData.jur_rep_legal
                    });
                }

                // Dirección
                Object.assign(this.direccion, {
                    dep_id: person.address.dep_id,
                    pro_id: person.address.pro_id,
                    dis_id: person.address.dis_id,
                    dir_calle_av: person.address.dir_calle_av
                });

                this.cargarProvincias();
                this.cargarDistritos();

                // Abogado
                const abogadoPersona = proceeding.abogado.persona;
                this.abogado = `${abogadoPersona.nat_apellido_paterno} ${abogadoPersona.nat_apellido_materno} ${abogadoPersona.nat_nombres}`;

                // Costos
                if (costos !== null) {
                    Object.assign(this.expediente, {
                        exp_monto_ejecucion1: costos.ex_ejecucion_1,
                        exp_monto_ejecucion2: costos.ex_ejecucion_2,
                        exp_interes1: costos.ex_interes_1,
                        exp_interes2: costos.ex_interes_2,
                        exp_costos: costos.ex_costos
                    });
                }
            } catch (error) {
                this.handleError("Error al cargar expediente:", error);
            }
        },

        async cargarDepartamentos() {
            try {
                const response = await this.get({
                    url: `${this.$store.getters.get__url}/department`,
                    token: this.$store.getters.get__token,
                    params: {}
                });

                this.departamentos = response.data;
                this.provincias = [];
                this.distritos = [];
            } catch (error) {
                this.handleError("Error al cargar departamentos:", error);
            }
        },

        async cargarProvincias() {
            if (this.direccion.dep_id) {
                try {
                    const response = await this.post({
                        url: `${this.$store.getters.get__url}/department/provincias`,
                        token: this.$store.getters.get__token,
                        params: {
                            dep_id: this.direccion.dep_id,
                        }
                    });

                    this.provincias = response.data;
                } catch (error) {
                    this.handleError("Error al cargar provincias:", error);
                }
            }
        },

        async cargarDistritos() {
            try {
                const response = await this.post({
                    url: `${this.$store.getters.get__url}/department/distritos`,
                    token: this.$store.getters.get__token,
                    params: {
                        pro_id: this.direccion.pro_id,
                    }
                });

                this.distritos = response.data;
            } catch (error) {
                this.handleError("Error al cargar distritos:", error);
            }
        },

        async cargarDistritosJudiciales() {
            try {
                const response = await this.get({
                    url: this.$store.getters.get__url + "/judicialdistrict",
                    token: this.$store.getters.get__token,
                    params: {},
                });
                this.distrito = response.data;
            } catch (error) {
                this.handleError("Error al cargar distritos", error);
            }
        },

        async cargarinstancias() {
            if (this.juzgado.dis_id) {
                try {
                    const response = await this.get({
                        url: this.$store.getters.get__url + "/instance",
                        token: this.$store.getters.get__token,
                        params: {},
                    });
                    this.instancia = response.data;
                } catch (error) {
                    this.handleError("Error al cargar instancias", error);
                }
            }
        },

        async cargarespecialidades() {
            if (this.juzgado.ins_id) {
                try {
                    const response = await this.get({
                        url: this.$store.getters.get__url + "/specialty",
                        token: this.$store.getters.get__token,
                        params: {},
                    });
                    this.especialidad = response.data;
                } catch (error) {
                    this.handleError("Error al cargar especialidades", error);
                }
            }
        },

        async update() {
            try {
                await this.post({
                    url: `${this.$store.getters.get__url}/proceeding/update`,
                    token: this.$store.getters.get__token,
                    params: {
                        expediente: this.expediente,
                        tipoper: this.tipoper,
                        procesal: this.procesal,
                        direccion: this.direccion,
                        pnat: this.pnat,
                        pjuc: this.pjuc,
                    },
                });

                this.$router.go(-1);
            } catch (error) {
                this.handleError("Error al actualizar el expediente", error);
            }
        },

        async cargarjuzgados() {
            try {
                const response = await this.post({
                    url: `${this.$store.getters.get__url}/juzgado`,
                    token: this.$store.getters.get__token,
                    params: {
                        judis_id: this.juzgado.dis_id,
                    }
                });

                this.juzgados = response.data;
            } catch (error) {
                this.handleError("Error al cargar los juzgados", error);
            }
        },

        async listarMaterias() {
            try {
                const response = await this.get({
                    url: `${this.$store.getters.get__url}/subject`,
                    token: this.$store.getters.get__token,
                });

                this.materias = response.data;
            } catch (error) {
                this.handleError("Error al obtener las materias", error);
            }
        },

        async listarPretensiones() {
            try {
                const response = await this.get({
                    url: `${this.$store.getters.get__url}/claim`,
                    token: this.$store.getters.get__token,
                });

                this.pretenciones = response.data;
            } catch (error) {
                this.handleError("Error al obtener las pretensiones", error);
            }
        },

        goBack() {
            this.$router.go(-1);
        }
    },
};
</script>